import { FormEvent } from "react";
import { Input } from "./input";
import { UserInfo } from "AppTypes";
import * as React from "react";

interface PersonalInfoProps {
  userInfo: UserInfo;
  updateUserInfo: (userInfo: UserInfo) => void;
  showRequired: boolean;
}

export const PersonalInfo = ({
  userInfo,
  updateUserInfo,
  showRequired,
}: PersonalInfoProps) => {
  const handlePersonalInfo = (
    event: FormEvent<HTMLInputElement>,
    key: keyof UserInfo
  ) => {
    const updatedUserInfo = { ...userInfo };
    updatedUserInfo[key] = event.currentTarget.value;
    updateUserInfo(updatedUserInfo);
  };

  return (
    <section className='flex flex-col gap-4 w-full'>
      <Input
        label='Name'
        placeholder='e.g. Stephen King'
        showRequired={showRequired && !userInfo.first_name}
        value={userInfo.first_name}
        onChange={(e: FormEvent<HTMLInputElement>) =>
          handlePersonalInfo(e, "first_name")
        }
      />

      <Input
        label='Email Address'
        type='email'
        placeholder='e.g. stephenking@lorem.com'
        showRequired={
          showRequired && (!userInfo.email || !userInfo.email.includes("@"))
        }
        value={userInfo.email}
        onChange={(e: FormEvent<HTMLInputElement>) =>
          handlePersonalInfo(e, "email")
        }
      />

      <Input
        label='Phone Number'
        placeholder='e.g. +1 234 567 890'
        showRequired={showRequired && !userInfo.phone}
        value={userInfo.phone}
        onChange={(e: FormEvent<HTMLInputElement>) =>
          handlePersonalInfo(e, "phone")
        }
      />
    </section>
  );
};
